import React, { Component } from "react";
import "./App.css";
import { isMobile } from "react-device-detect";
import minbox from "./assets/SVG/mintbox_logo.svg";
import { BrowserRouter, BrowserRouter as Router, Redirect, Route, Switch,Link } from 'react-router-dom';
import HomeLayout from "./views/mobileView/route";
import History from "./History";

function Home() {
  return <h2>Home</h2>;
}

function About() {
  return <h2>About</h2>;
}

function Users() {
  return <h2>Users</h2>;
}


class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      load: true,
    };
  }

  componentDidMount() {
    this.setState({
      load: false,
    });
  }


  render() {
    if (isMobile) {
      return (
        <Router>
        <div>
          <nav>
            <ul>
              <li>
                <Link to="/">Home</Link>
              </li>
              <li>
                <Link to="/about">About</Link>
              </li>
              <li>
                <Link to="/users">Users</Link>
              </li>
            </ul>
          </nav>
  
          {/* A <Switch> looks through its children <Route>s and
              renders the first one that matches the current URL. */}
          <Switch>
            <Route path="/about">
              <About />
            </Route>
            <Route path="/users">
              <Users />
            </Route>
            <Route path="/">
              <Home />
            </Route>
          </Switch>
        </div>
      </Router>
        // <BrowserRouter basename="/" history={History}>
        //     {/* <div>
        //       <Switch> */}
        //         {/* <ProtectedAdminRoute path="/admin" component={Admin} />
        //         <ProtectedSellerRoute path="/seller" component={SellerAdmin} />
        //         <ProtectedUserRoute path="/user" component={UserRouter} /> */}
        //         <HomeLayout />
        //       {/* </Switch>
        //     </div> */}
        //   </BrowserRouter>
      );
    } else {
      return (
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            minHeight:"90vh",
            alignItems:"center",
            justifyContent:"center"
          }}
        >
          <div>
            <img src={minbox} height="100px" width="100%" />
          </div>
          <h1 style={{ textAlign: "center" }}>HEY! WE'RE BUILDING OUR WEBSITE ... </h1>
        </div>
      );
    }
  }
}

export default App;
